// Obtención de valores
const menu        = document.querySelectorAll('.icon');
let nav           = document.querySelector('#nav'); 
const img         = document.querySelectorAll('.galery__img');
const ligthBox = document.querySelector('#ligthbox');
let imgLigthBox   = document.querySelector('#ligthbox-img');
const btnLigthBox = document.querySelector('#ligthbox-clouse');

// NAV
const navScroll = document.querySelector('#nav-container');
const info = document.querySelector('#info');
const body = document.querySelector('body');

// funcion del menu
const menuToggle = ()=>{
    nav.classList.toggle('nav__principal--active');
}

// invocacion de la función que abre y cierra el menu

menu.forEach( (e)=> {
    e.addEventListener('click', menuToggle);
});

// funcion que inserta la direccion de imagen del ligthBox
img.forEach((e)=>{
    e.addEventListener('click', ()=>{
        imgLigthBox.src = e.src;

        ligthBox.classList.add('ligthbox--active');
        body.classList.add('bodyStop');
    })
})

const ligthBoxDesabeld = ()=>{
    ligthBox.classList.remove('ligthbox--active');
    body.classList.remove('bodyStop');
}

// funcion de cierre del boton del ligthBox
btnLigthBox.addEventListener('click', ligthBoxDesabeld);
ligthBox.addEventListener('click', ligthBoxDesabeld);

// Funcion que hará que el nav se quede fijo en cierto lugar

window.addEventListener('scroll', ()=>{
    let ubicacionDiv = info.getBoundingClientRect();

    // console.log(ubicacionDiv.top)

    if(ubicacionDiv.top <= 0){
        navScroll.classList.add('header__nav--fixed');
        body.classList.add('body-scroll');
    }else{
        navScroll.classList.remove('header__nav--fixed');
        body.classList.remove('body-scroll');
    }
})